import React from "react"
import Description from "./featured_item_description"

export default function ImageWithDescription({children, renderPos, imageTitle, imageUrl }) {

    const renderJSX =  (       
        <div className="fifty-split">
            <h4>{imageTitle}</h4>
            <div>
                <img src={imageUrl} style={{width:"100%", height:"auto"}} loading="lazy"/>
            </div>
        </div>
    )

    var leftRenderPos = "";
    var rightRenderPos = "";
    if(renderPos=="left"){
        leftRenderPos = renderJSX;
    }else{
        rightRenderPos = renderJSX;
    }

return (

    <div className="container flex-container">

        {leftRenderPos}

        <div className="description thirty-split">
            <Description>
                {children}
            </Description>
        </div>
   
        {rightRenderPos}
      
    </div>

)}