import React from "react"
import PageSplitBanner from "./page_split_banner"

export default function SectionWithHeading({ children, headingText, cssStylingClass }) {

return (

    <section className={cssStylingClass}>

        <PageSplitBanner text={headingText}/>

        <div className="container">

            {children}

        </div>

    </section>
)}