import { AgGridReact } from "ag-grid-react"
import 'ag-grid-enterprise'
import React, {useEffect, useState, useRef} from "react"

export default function ApiExplorerSection() {


    const [rowData, setRowData] = useState([]);
    const [apiCode, setApiCode] = useState("Click the buttons below \nto see the API in action.");
    
    const gridRef = useRef();

    const columnDefs = [
        {headerName: 'Athlete', field: 'athlete', width: 150, sortable: true},
        {headerName: 'Age', field: 'age', width: 90, sortable: true},
        {headerName: 'Country', field: 'country', width: 120, filter: 'agTextColumnFilter', sortable: true},
        {headerName: 'Year', field: 'year', width: 90, sortable: true},
        {headerName: 'Sport', field: 'sport', width: 110, sortable: true},
        {headerName: 'Gold', field: 'gold', width: 100, aggFunc: 'sum', sortable: true},
    ];


    useEffect(() => {
        fetch('https://raw.githubusercontent.com/ag-grid/ag-grid-docs/master/src/olympicWinnersSmall.json')
            .then(result => result.json())
            .then(rowData => setRowData(rowData))
    }, []);




    
    const sortByOneColumn = ()=>{
        setApiCode('columnApi.applyColumnState({state:[\n' +
        '    {colId: \'country\', sort: \'asc\'}\n' +
        ']});');
        const columnApi = gridRef.current.columnApi;
        columnApi.applyColumnState({state:[{colId: 'country', sort: 'asc'}]});
    };

    const sortByTwoColumns = ()=>{
        setApiCode('columnApi.applyColumnState({state:[\n' +
        '    {colId: \'country\', sort: \'asc\'},\n' +
        '    {colId: \'year\', sort: \'asc\'}\n' +
        ']});');
        const columnApi = gridRef.current.columnApi;
        columnApi.applyColumnState({state:[{colId: 'country', sort: 'asc'}, {colId: 'year', sort: 'asc'}]});
    };

    const clearSorting = ()=>{
        setApiCode('columnApi.applyColumnState(\n{defaultState:{sort: null}}\n);');
        const columnApi = gridRef.current.columnApi;
        columnApi.applyColumnState({defaultState:{sort: null}});
    }


    const setFilterByOneColumn = ()=>{
        setApiCode('api.setFilterModel(\n{country: {filterType:\'text\',\n type:\'equals\', filter:\'United States\'}\n});');
        const api = gridRef.current.api;
        api.setFilterModel({country: {filterType:'text', type:'equals', filter:'United States'}});
    }

    const removeFilter = ()=>{
        setApiCode('api.setFilterModel(null);');
        const api = gridRef.current.api;
        api.setFilterModel(null);
    }

    const groupByThreeColumns = () =>{
        setApiCode('columnApi.setRowGroupColumns(\n' +
        '    [\'country\', \'year\', \'sport\']\n' +
        ');');
        const columnApi = gridRef.current.columnApi;
        columnApi.setRowGroupColumns(['country', 'year', 'sport']);
    }

    const expandTopLevelRows = () =>{
        setApiCode('api.forEachNode(function (node) {\n' +
        '    if (node.level === 0) {\n' +
        '        node.setExpanded(true);\n' +
        '    }\n' +
        '});');
        const api = gridRef.current.api;
        api.forEachNode(function (node) {
            if (node.level === 0) {
                node.setExpanded(true);
            }
        });
    }

    const collapseTopLevelRows = () =>{
        setApiCode('api.forEachNode(function (node) {\n' +
        '    if (node.level === 0) {\n' +
        '        node.setExpanded(false);\n' +
        '    }\n' +
        '});');
        const api = gridRef.current.api;
        api.forEachNode(function (node) {
            if (node.level === 0) {
                node.setExpanded(false);
            }
        });
    }


    const removeGrouping = () =>{
        setApiCode('columnApi.setRowGroupColumns([]);');
        const columnApi = gridRef.current.columnApi;
        columnApi.setRowGroupColumns([]);
    }

    const setRowHeight = () =>{
        setApiCode('const rowNode = api.getDisplayedRowAtIndex(3);\n' +
        'rowNode.setRowHeight(100);\n' +
        'api.onRowHeightChanged();');
        const api = gridRef.current.api;
        const rowNode = api.getDisplayedRowAtIndex(3);
        rowNode.setRowHeight(100);
        api.onRowHeightChanged();
    }

    const resetRowHeight = () =>{
        setApiCode('api.resetRowHeights();');
        const api = gridRef.current.api;
        api.resetRowHeights();
    }




return (

<section className="api">
    <h2 className="banner">Easy to integrate and customize</h2>
    <div className="container">
        <div className="grid-example">
            <h4>Click operation on right and see the API in action</h4>
            <div className="grid-container ag-theme-balham-dark" style={{height: '500px', width: '100%'}}>
                <AgGridReact
                    columnDefs = {columnDefs}
                    rowData = {rowData}
                    enableRangeSelection = {true}
                    animateRows = {true}
                    suppressAggFuncInHeader = {true}

                    ref={gridRef}
                ></AgGridReact>
            </div>
        </div>
        <div className="api-methods-container">
            <div className="api-code">
                <pre>{apiCode}</pre>
            </div>
            <div>
                <img alt="Prompt to click on API links" src="assets/images/prompt-to-click-on-api-links.jpg" />
                <ul className="api-operations">
                    <li data-action="sort-by-one-column"><button onClick={sortByOneColumn} >Sort by one column (Country)</button></li>
                    <li data-action="sort-by-two-columns"><button onClick={sortByTwoColumns}>Sort by two columns (Country &amp; Year)</button></li>
                    <li data-action="clear-sorting"><button onClick={clearSorting}>Clear sorting</button></li>
                </ul>
                <ul className="api-operations">
                    <li data-action="set-filter-by-one-column"><button onClick={setFilterByOneColumn}>Filter by one column (United States)</button></li>
                    <li data-action="remove-filter"><button onClick={removeFilter}>Clear filtering</button></li>
                </ul>
                <ul className="api-operations">
                    <li data-action="group-by-three-columns">
                        <button onClick={groupByThreeColumns}>Group by three columns (Country, Year &amp; Sport)</button>
                    </li>
                    <li data-action="expand-top-level-rows"><button onClick={expandTopLevelRows}>Expand top level rows</button></li>
                    <li data-action="collapse-top-level-rows"><button onClick={collapseTopLevelRows}>Collapse top level rows</button></li>
                    <li data-action="remove-grouping"><button onClick={removeGrouping}>Remove grouping</button></li>
                </ul>
                <ul className="api-operations">
                    <li data-action="set-row-height"><button onClick={setRowHeight}>Set row height</button></li>
                    <li data-action="reset-row-height"><button onClick={resetRowHeight}>Reset row height to default</button></li>
                </ul>
            </div>
        </div>
    </div>
</section>

)}