import React from "react"
export default function CenteredLinkButton({children, text, href, colour }) {

    var buttonColourClass = 'btn-alt';

    if(colour=="blue"){
        buttonColourClass = '';
    }else{
        buttonColourClass = 'btn-alt';
    }

return (

    <div className="btn-wrapper">
        <a className={`btn btn-primary ${buttonColourClass}`} href={href}>
            {text}
        </a>
    </div>

)}