import React from "react"
import Description from "./featured_item_description"

export default function VideoWithDescription({children, videoPos, videoTitle, youtubeUrl }) {

    const videoJSX =  (       
        <div className="video-example fifty-split">
            <h4>{videoTitle}</h4>
                <div className="contains-responsive-video">
                    <iframe width="560" height="315" loading="lazy" src={youtubeUrl} title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
            </div>
        </div>
    )

    var leftVideoPos = "";
    var rightVideoPos = "";
    if(videoPos=="left"){
        leftVideoPos = videoJSX;
    }else{
        rightVideoPos = videoJSX;
    }

return (

    <section>



    <div className="container flex-container">

        {leftVideoPos}

        <div className="description thirty-split">
            <Description>
                {children}
            </Description>
        </div>
   
        {rightVideoPos}
      
    </div>
    </section>

)}